import "./styles.css";
import { useState } from "react";
export default function App() {
  const [input, setInput] = useState([]);
  const [even, setEven] = useState([]);
  const [odd, setOdd] = useState([]);
  const [extra, setExtra] = useState();
  var a = [];
  var b = [];
  function validate(e) {
    setInput(e.target.value);
  }
  function vali(e) {
    if (input.length > 0) {
      const reg = /^[0-9\b]+$/;
      if (e.target.value === "" || reg.test(e.target.value)) {
        const x = [...input];
        console.log(x);
        let flag = false;
        for (var i = 0; i < x.length; i++) {
          console.log(x[i]);

          if (x[i] % 2 == 0) {
            flag = true;
            a.push(x[i]);
          } else {
            b.push(x[i]);
          }
        }
        setEven(a.length);
        setOdd(b.length);
        setInput("");
      }
    }
  }

  return (
    <div className="App">
      {/* <p>{validate}</p> */}
      <p>even:{even}</p>
      <p>odd:{odd}</p>
      <p>{input}</p>
      <input type="text" onChange={validate} onBlur={vali} />
    </div>
  );
}
